@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600&display=swap);
header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ========================= CV ========================= */

.cv {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

/* ========================= SOCIALS ========================= */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 2px;
  border: 1px solid transparent;
  border-radius: 3px;
  height: 2rem;
  background: var(--color-p);
}

/* ========================= IMG ========================= */

.me {
  background: linear-gradient(var(--color-p), transparent);
  width: 22rem;
  height: 40rem;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 8rem 8rem 0 0;
  overflow: hidden;
  padding: 0 0 1.5rem 0;
}

/* =================== SCROLL DOWN =================== */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}

nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-c);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background: var(--color-p);
  color: var(--color-bg);
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about__me {
  width: 100%;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-p), transparent);
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: var(--transition);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: white;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 6rem auto 9rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .about__me {
    width: 55%;
    margin: 4rem auto 6rem;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}

/* Using exp.css for same styling */

.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.experience__container > div {
  background: var(--color-bg);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 3px solid transparent;
  transition: var(--transition);
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-p);
  cursor: default;
  transition: var(--transition);
}
.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: white;
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience__details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience__details-icon {
  margin-top: 6px;
  color: var(--color-p);
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }

  .experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }

  .experience__content {
    padding: 1rem;
  }
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .experience__container {
    grid-gap: 1rem;
    gap: 1rem;
  }

  .experience__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}

article > h5 {
  color: var(--color-lg);
}

.contact__options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  /* width: 50%; */
}

.contact__option {
  background: var(--color-bg);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 3px solid transparent;
  transition: var(--transition);
}

.contact__option:hover {
  background: transparent;
  transition: var(--transition);
  border-color: var(--color-p);
  cursor: default;
}

.contact__option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact__option > a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .container.contact__container {
    width: var(--container-width-sm);
  }
}

footer {
  background: var(--color-p);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__socials {
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-bg);
  color: white;
  padding: 0.8rem;
  display: flex;
  border: 2px solid transparent;
  border-radius: 0.6rem;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  .footer__socials {
    margin-bottom: 2.6rem;
  }
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #6e44ff;
  --color-p: #b892ff;
  --color-c: #ffc2e2;
  --color-lg: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Nunito", sans-serif;
  background: #6e44ff;
  background: var(--color-bg);
  color: white;
  line-height: 1.7;
  background-image: url(/portfolio/static/media/bg-texture.28424986.png);
}

::-webkit-scrollbar {
  display: none;
}

/* =================== GENERAL =================== */

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: white;
}

section > h2 {
  color: #ffc2e2;
  color: var(--color-c);
  margin-bottom: 3rem;
  bottom: 2rem;
  position: relative;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-lg);
}

.text-candy {
  color: #ffc2e2;
  color: var(--color-c);
}

a {
  color: #ffc2e2;
  color: var(--color-c);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: white;
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #b892ff;
  color: var(--color-p);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #ffc2e2;
  border: 1px solid var(--color-c);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background: white;
  color: #6e44ff;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #b892ff;
  background: var(--color-p);
  color: white;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }
}

section {
  margin-top: 6rem;
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }
}

section > h2 {
  margin-top: 2rem;
}

