header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ========================= CV ========================= */

.cv {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ========================= SOCIALS ========================= */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 2px;
  border: 1px solid transparent;
  border-radius: 3px;
  height: 2rem;
  background: var(--color-p);
}

/* ========================= IMG ========================= */

.me {
  background: linear-gradient(var(--color-p), transparent);
  width: 22rem;
  height: 40rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 8rem 8rem 0 0;
  overflow: hidden;
  padding: 0 0 1.5rem 0;
}

/* =================== SCROLL DOWN =================== */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
