@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #6e44ff;
  --color-p: #b892ff;
  --color-c: #ffc2e2;
  --color-lg: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Nunito", sans-serif;
  background: var(--color-bg);
  color: white;
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
}

::-webkit-scrollbar {
  display: none;
}

/* =================== GENERAL =================== */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: white;
}

section > h2 {
  color: var(--color-c);
  margin-bottom: 3rem;
  bottom: 2rem;
  position: relative;
}

.text-light {
  color: var(--color-lg);
}

.text-candy {
  color: var(--color-c);
}

a {
  color: var(--color-c);
  transition: var(--transition);
}

a:hover {
  color: white;
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-p);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-c);
  transition: var(--transition);
}

.btn:hover {
  background: white;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-p);
  color: white;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* =================== MEDIA (medium devices) =================== */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
}

section {
  margin-top: 6rem;
}

/* =================== MEDIA (small devices) =================== */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
}

section > h2 {
  margin-top: 2rem;
}
